@import url(https://fonts.googleapis.com/css?family=Ranchers);
body {
  background: #2b5876;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #4e4376, #2b5876); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
 margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.grid-container {
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
}

.grid-container > div {
  text-align: center;
  padding: 20px 0;
  font-size: 30px;
  border-bottom: solid 2px white;
  padding-bottom: 10px;
}




.logo{
  border-radius: 40%;
}

.bg{
  font-size: 20px;
}
.bg:hover{
  color: white;
}



nav{
  padding: 10px;
  float: right;
}
 
a{
  text-decoration: none;
  color: aliceblue;
}
.des{
  font-size: 20px;
  font-weight: bold;

}
.social:hover{
  color:white;
  -webkit-animation: pulse 3s;
          animation: pulse 3s;
}
.container{
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  border-bottom: solid 2px white;
  padding-bottom: 10px;
}

.secondary{
  background-color: #f6853f; /* Green */
  border-radius: 40%;
  float: left;
  margin: 3px 1px;
  padding: 6px;
  text-align: center;
  font-size: 20px;
}

.first{
  background-color: #f6853f;
  border-radius: 40%;
  float: right;
  margin: 3px 1px;
  padding: 6px;
  text-align: center;
  font-size: 20px;

}
  h1{
    border-bottom: solid 2px white;
    font-family: 'Ranchers';
    font-weight: 150;
    color: #110b0b;
  }


.slid{
    margin: auto;
  width: 70%;
  padding: 10px;
}
.particles{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -500;
  }
